import React, { Component } from 'react';
import styled from 'styled-components';
import Typing from 'react-typing-animation';
import DownArrow from './DownArrow';

const HomeWrapper = styled.div`
  font-family: 'Open Sans', sans-serif;
  color: #888;
  // background-color: pink;
  height: 102vh;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  @media (max-width: 600px) {
    justify-content: center;
  }
`;

const IntroWrapper = styled.div`
  // background-color: orange;
  width: 60vw;
  max-width: 1300px;
  margin-left: 10vw;
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 2.5em;

  @media (max-width: 600px) {
    text-align: center;
    margin: 0 15px;
    width: 100vw;
    font-size: 3.1em;
  }

  @media (min-width: 1800px) {
    font-size: 50px;
  }
`;

const Intro = styled.h1`
  margin: 0;
  text-transform: uppercase;
`;

class Home extends Component {
  componentDidMount() {
    const cursor = document.getElementsByClassName('efaGep');

    // hide cursor
    function hideCursor() {
      cursor[0].style.visibility = 'hidden';
    }
    // hide cursor after 10s
    setTimeout(hideCursor, 9500);
  }

  render() {
    return (
      <div>
        <HomeWrapper>
          <IntroWrapper>
            <Typing startDelay={1000} speed={75}>
              <Intro>HI!</Intro>
              <Typing.Delay ms={1500} />
              <Intro>I&#39;m an Experience-Focused UX Engineer based in Singapore.</Intro>
            </Typing>
          </IntroWrapper>
        </HomeWrapper>
        <DownArrow />
      </div>
    );
  }
}

export default Home;



